body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#editor {
  width: 100%;
  height: calc(100% - 25px);
  background: #212121;
  resize: none;
  padding: 1em;
  overflow: auto;
  border: none;
  scrollbar-color: #313131 black;
  grid-area: editor;
}

div {
  border-right: 2px solid #757575;
}
#preview {
  overflow: auto;
  word-wrap: normal;
  height: calc(100% - 25px);
  padding: 0.5em;
  scrollbar-color: #313131 black;
  grid-area: preview;
}

#preview code {
  font-family: monospace;
  background: #313131;
}

#preview pre code {
  display: block;
  border-radius: 5px;
  padding: 0.5em;
}

#preview table,
#preview table td,
#preview table th {
  padding: 0.25em;
  border-collapse: collapse;
  border: 1px solid #f5f5f5;
}

#preview a {
  color: #4f8cc9;
}

#preview blockquote {
  background: #313131;
  border-left: 5px solid #aaa;
  margin: 1.5em 10px;
  padding: 0.5em 10px;
}

#preview blockquote p {
  display: inline;
}
footer {
  background: #007acc;
  grid-area: footer;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 0 0.5em;
}

footer small {
  line-height: 20px;
}
* {
  box-sizing: border-box;
  color: #f5f5f5;
}

#main {
  background: #212121;
  width: 100vw;
  height: 100vh;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: calc(100vh - 20px);
  grid-template-areas: "editor preview" "footer footer";
  overflow: hidden;
}

.title {
  padding-left: 0.5em;
  height: 25px;
  line-height: 25px;
  font-size: 18px;
  background: #424242;
  margin: 0;
}
